@import url('https://fonts.googleapis.com/css2?family=Oxygen&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap');

* {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Oxygen', sans-serif;
    background-color: rgb(22, 22, 22);

}


.header-container {
    display: flex;
    background: rgb(22, 22, 22);
    background-size: cover;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    color: rgb(236, 236, 236);
    
}


.header-flex {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    gap: 10vh;
    margin-top: 40vh;
}

.header-txt {
    display: flex;
    flex-direction: column;
    gap: 1px;
    padding: 0px;
    margin-top: 10vh;
}

button {
    cursor: pointer;
}

.code-logo {
    height: 10vh;
    width: 10vw;
}

.fullName {
    margin-bottom:  0vh;
    font-family: 'Roboto', sans-serif;
    font-size: 2.5rem;
    letter-spacing: 1.6px;
}

.profession {
    margin-top: 0vh;
    align-self: flex-end;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
    font-size: 1.5rem;
}

.projects-btn {
    overflow: hidden;
}

.enter-btn {
    background:rgba(24, 24, 24, 0.445); 
    color: rgb(243, 243, 243);
    border: 1.9px;
    border-color: rgb(238, 238, 238);
    border-style: solid;
    padding: 1vh;
    width: 11vw;
    font-family: 'Oxygen', sans-serif;
    font-size: 1.1rem;
    letter-spacing: 1.6px;
    transition: all 300ms ease-in-out;
}

  
.enter-btn:hover {
    background:rgba(212, 212, 212, 0.945); 
    border-color: rgba(212, 212, 212, 0.945); 
    color: rgb(73, 73, 73);
}

@media (max-width: 500px) {
    .enter-btn {
        width: 10em;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .header-container {
        max-width: 100vw;
    }


}