@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap');
.container-aboutMe {
    background-color: rgb(22, 22, 22);
    display: flex;
    flex-direction: column;
    color: white;
    gap: 30px;
    margin-top: 30vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

}

.aboutMe-title {
    font-size: 1.9rem;
    padding-top: 8vh;
    color: rgb(240, 240, 240);
    font-family: 'Montserrat', sans-serif;
}

.userIcon {
    min-height: 10vh;
    border: 3px solid;
    padding: 1.5em;
    color: rgb(62, 62, 177);
}

.intro-box {
    display: flex;
    padding: 3vh;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    gap: 5em;
}

.skill-box > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: .8rem;
    font-family: 'Roboto', sans-serif;
    gap: 5px;
    color: #e6e6e6;
}


.iconBrand {
    height: 50px;
    width: 50px;
}

.htmlLogo {
    color: #e54d26;;
}

.cssLogo {
    color: #3d8fc6;
}

.reactLogo {
    color: #61dafb;
}

.jsLogo {
    color: #f0db4f;;
}

.githubLogo {
    color: white;
}

.gitLogo {
    color: #f34f29;
}

.logoSkill {
    height: 50px;
    width: 50px;
}

.npmLogo {
    color: #cb3837;
}

.skill-box {
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1Fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 2vw;
    
}

.para-intro {
    width: 60vh;
    text-align: justify;
    line-height: 1.5em;
    font-size: 1.2rem;
    color: rgb(247, 247, 247);
    
}


@media (max-width: 500px) {
    .para-intro {
        font-size: 1rem;
        width: 80vw;
        text-align: center;
        color: rgb(247, 247, 247);
    }

    .container-aboutMe {
        max-width: 100vw;
    }
    
    .aboutMe-title {
        max-width: 100vw;
     
    }
}

