footer {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    background: rgb(17, 17, 17);
    color: white;
    justify-content: center;
    align-items: center;
    gap: 10px;
    position: absolute; 
    width: 100%;
    padding-bottom: 15px;
    padding-top: 15px;
}

.contact-title {
    letter-spacing: 0.2em;
}

span.contact {
    color: rgb(71, 87, 236);
}

a:hover {
    font-weight: bold;
}

a {
    text-decoration: none;
    color: rgb(71, 87, 236);
}


@media (max-width : 500px) {
    #footer {
        padding-top: 15px;
        padding-bottom: 15px;
        max-width: 100vw;
    }
}