@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap');

.container-projects {
    background-color: rgb(22, 22, 22);
    display: flex;
    flex-direction: column;
    margin-top: 20vh;
    color: white;
    gap: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.projects-title {
    text-align: center;
    font-size: 1.9rem;
    padding-top: 8vh;
    color: rgb(240, 240, 240);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
}

.projects-box {
    display: grid;
    justify-content: center;
    align-items: flex-start;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 2fr 2fr 2fr 2fr;
    gap: 5em;
    padding: 10em;
    width: 50vw;
    
}

.previewImg {
    object-fit: fill;
    height: 30vh;
    width: 45vh;
    border: 2px solid;
    border-color: rgb(62, 62, 177);
    border-style:dashed;
    opacity: 0.6;
}

.description {
    justify-self:flex-start;
    display: flex;
    gap: 1em;
    flex-direction: column;
    font-size: 2.2rem;
    color: rgb(62, 62, 177);
  
}

.description > p {
    font-size: 1.2rem;
    color: white;
}

.preview-btn {
    width: 10em;
    height: 4vh;
    font-size: 0.8rem;
    background: transparent;
    color: white;
    background:  rgba(68, 122, 223, 0);
    border: none;
    border-radius: 2em;
    transition: all 0.3s ease-in-out;
    border: 2px solid rgb(53, 164, 238);;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;

}

.preview-btn:hover {
    background-color:rgb(53, 164, 238);

}

.preview-box {
    display: flex;
    gap: 1em;
    align-items: center;
}

.githubLogoPrev {
    height: 4vh;
    color: white;
}

.preview-box > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2px;
    cursor: pointer;
}

.preview-box > div > p {
    font-size: 0.8rem;
    color: white;
    letter-spacing: 0.2em;
}


@media (max-width: 500px) {
    .projects-box {
        display: grid;
        grid-template-columns: 1fr;
        max-width: 100vw;
        padding: 0;
    }
    .container-projects {
        max-width: 100vw;
    }

    .description {
        max-width: 100vw;
    }
    .projects-title {
        max-width: 100vw;
     
    }
}